<template>
  <h1>TodoList</h1>
  <TodoInput @add-todo="addTodo"></TodoInput>
  <DarkMode @dark-mode="switchToDarkMode"></DarkMode>
  <div class="todos-container">
    <TodoItem
      :class="{ 'completed': todo.completed }"
      v-for="(todo, index) in todos" 
      :key="index" 
      :todo="todo.todo"
      @todo-completed="completeTodo(todo)"
      @todo-editing="editTodo(todo, $event)"
      @todo-deleted="deleteTodo(todo)">
    </TodoItem>
  </div>
</template>

<script>
import TodoItem from './components/TodoItem'
import TodoInput from './components/TodoInput'
import DarkMode from './components/DarkMode'
import { ref } from 'vue'

export default {
  name: 'App',
  components: {
    TodoItem,
    TodoInput,
    DarkMode
  },
  setup() {
    const todos = ref([])
    let darkMode = ref(false)

    function addTodo(newTodo) {
      if (newTodo != '') todos.value.push({ id: todos.value.length + 1, todo: newTodo, completed: false })
    }

    function completeTodo(completedTodo) {
      completedTodo.completed = !completedTodo.completed
    }

    function deleteTodo(deletedTodo) {
      todos.value = todos.value.filter(todo => todo != deletedTodo)
    }

    function editTodo(editingTodo, newTodo) {
      editingTodo.todo = newTodo
    }

    function switchToDarkMode() {
      darkMode.value = !darkMode.value

      document.body.classList.toggle('dark-mode')
    }

    return {
      todos,
      darkMode,
      addTodo,
      completeTodo,
      deleteTodo,
      editTodo,
      switchToDarkMode
    }
  }
}
</script>

