<template>
  <div class="todo-container">
    <div class="text">
      <p 
        @click="completeTodoEvent" 
        v-if="!isEditing">
        {{ todo }}
      </p>
      <input 
        v-if="isEditing" 
        type="text" 
        v-model="newTodoInput"
        @keyup.enter="editTodoBtn">
    </div>
    <div class="btns">
      <button 
        class="edit-btn"
        @click="editTodoBtn">
        <i class="fas fa-edit"></i>
      </button>
      <button 
        class="delete-btn"
        @click="deleteTodoEvent">
        <i class="fas fa-trash"></i>
      </button>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'

export default {
  name: 'TodoItem',
  props: {
    todo: {
      type: String,
      required: true
    }
  },
  setup(props, ctx) {
    const isEditing = ref(false)
    const newTodoInput = ref('')

    function completeTodoEvent() {
      ctx.emit('todo-completed')
    }

    function deleteTodoEvent() {
      ctx.emit('todo-deleted')
    }

    function editTodoBtn() {
      isEditing.value = !isEditing.value

      if (!isEditing.value) {
        ctx.emit('todo-editing', newTodoInput.value)
      } else {
        newTodoInput.value = props.todo
      }
    }

    return {
      isEditing,
      newTodoInput,
      completeTodoEvent,
      deleteTodoEvent,
      editTodoBtn
    }
  }
}
</script>

