<template>
  <form>
    <input 
      type="text" 
      v-model.lazy="todoInput" 
      placeholder="Create a new to-do"
      @keypress.enter="addTodoEvent"
    >
    <button @click.prevent="addTodoEvent">
      <i class="fas fa-plus"></i>
      add
    </button>
  </form>
</template>

<script>
import { ref } from 'vue'

export default {
  name: 'TodoInput',
  setup(_, ctx) {
    let todoInput = ref('')

    function addTodoEvent() {
      // i'm giving the input value as second argument in the emit so i can use it later in the addTodo() function to add a todo
      ctx.emit('add-todo', todoInput.value)
      todoInput.value = ''
    }

    return {
      todoInput,
      addTodoEvent
    }
  }
}
</script>

<style lang="sass" scoped>

form 
  display: flex
  align-items: center
  width: 800px

  input
    width: 680px
    height: 35px
    border: solid 1.5px $inputBorderColor
    border-radius: 5px
    transition: box-shadow 200ms ease
    outline: none

    &[placeholder="Create a new to-do"]
      padding-left: 20px

    &:focus
      box-shadow: 0px 0px 5px 1.5px $blue
      border-color: none

  button 
    @include btn
    background: $blue
    text-transform: uppercase
    color: white
    margin-left: 8px
    width: 80px
    height: 35px
    border: solid 3px $blue
    border-radius: 5px
    transition: all 300ms ease
    display: flex
    justify-content: center
    align-items: center
    font-weight: bold

    i
      padding-right: 5px

    &:hover
      background: none
      color: $blue

</style>