<template>
  <div class="dark-mode-btn">
    <button @click="sendDarkModeEvent">
      Dark Mode
      <i class="fas fa-moon"></i>
    </button>
  </div>
</template>

<script>
export default {
  name: 'DarkMode',
  setup(_, ctx) {
    function sendDarkModeEvent() {
      ctx.emit('dark-mode')
    }

    return { sendDarkModeEvent }
  }
};
</script>

<style lang="sass" scoped>

.dark-mode-btn
  position: absolute
  bottom: 5px
  left: 5px

  button
    @include btn
    background: $blue
    color: white
    border: 3px solid $blue
    border-radius: 5px
    transition: background 300ms ease
    padding: 10px
    margin-top: 20px

    &:hover 
      background: none
      color: $blue

</style>
